<!--代驾话术检查-->
<!--done-->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3>目录</h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="requests_num_a" @click="goTag('#requests_num')" class=""><a>代驾话术检查请求数</a></li>
          <li id="succ_num_a" @click="goTag('#succ_num')"><a>代驾话术检查成功数</a></li>
          <li id="fail_num_a" @click="goTag('#fail_num')"><a>代驾话术检查失败数</a></li>
          <li id="downfail_num_a" @click="goTag('#downfail_num')"><a>代驾话术检查语音下载失败数</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header">代驾话术检查</h1>

        <div class="form-container col-sm-12 col-md-11">
          <!--判责请求数-->
          <div id="requests_num" class="form" ></div>
          <!--判责成功数-->
          <div id="succ_num" class="form"></div>
          <!--判责失败数-->
          <div id="fail_num" class="form"></div>
          <!--语音下载失败数-->
          <div id="downfail_num" class="form"></div>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "judgeDjhs",
    data() {
      return {
        data: {
          requests_num: {},
          succ_num: {},
          fail_num: {},
          downfail_num: {},
        },
      }
    },
    mounted() {
      // 先画框架，并设置showLoading
      this.draw_requests_num();
      this.draw_succ_num();
      this.draw_fail_num();
      this.draw_downfail_num();

      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.requests_num.showLoading();
        this.succ_num.showLoading();
        this.fail_num.showLoading();
        this.downfail_num.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/requestforcancel',
          {
            params: {
              page: 'judge_djhs',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
      draw_requests_num() { // 判责请求数
        this.requests_num = this.$echarts.init($("#requests_num")[0]);
        // 绘制图表1
        this.requests_num.setOption({
          title: {
            text: '话术检查请求数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'请求数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '请求数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.requests_num.showLoading();
      },
      draw_succ_num() { // 取消判责成功数
        this.succ_num = this.$echarts.init($("#succ_num")[0]);

        this.succ_num.setOption({
          title: {
            text: '话术检查成功数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'成功数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '成功数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.succ_num.showLoading();
      },
      draw_fail_num() { // 取消判责失败数
        this.fail_num = this.$echarts.init($("#fail_num")[0]);
        this.fail_num.setOption({
          title: {
            text: '话术检查失败数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'失败数'}, {name:'调用号码保护平台失败数'}, {name:'未产生通话数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {
            type : 'value',
          },
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '失败数',
            type: 'line',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          },
            {
              name:'调用号码保护平台失败数',
              type:'line',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            },
            {
              name:'未产生通话数',
              type:'line',
              barGap:'50%',
              barCategoryGap:'50%',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }]
        });
        this.fail_num.showLoading();
      },
      draw_downfail_num() { // 语音下载失败数
        this.downfail_num = this.$echarts.init($("#downfail_num")[0]);
        this.downfail_num.setOption({
          title: {
            text: '话术检查语音下载失败数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'语音下载失败数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '语音下载失败数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.downfail_num.showLoading();
      },


      fillData() {
        this.requests_num.hideLoading();
        this.succ_num.hideLoading();
        this.fail_num.hideLoading();
        this.downfail_num.hideLoading();

        // 填入数据
        this.requests_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '请求数',
            data: this.data.pv
          }]
        });

        // 填入数据
        this.succ_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '成功数',
            data: this.data.succ
          }]
        });
        this.fail_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '失败数',
            data: this.data.fail
          },
            {
              // 根据名字对应到相应的系列
              name: '调用号码保护平台失败数',
              data: this.data.datanull
            },
            {
              // 根据名字对应到相应的系列
              name: '未产生通话数',
              data: this.data.recordempty
            }
          ]
        });

        this.downfail_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '语音下载失败数',
            data: this.data.downfail
          }]
        });

      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>





