                    <li id="sp_czc"><router-link class="router-link" :to="{name: 'speechCzc'}">识别出租车</router-link></li>
                    <li id="sp_hhc"><router-link class="router-link" :to="{name: 'speechHhc'}">识别豪华车</router-link></li>
                    <li id="sp_dj"><router-link class="router-link" :to="{name: 'speechDj'}">识别代驾</router-link></li>
                    <li id="sp_zc"><router-link class="router-link" :to="{name: 'speechZc'}">识别专车</router-link></li>
                  </ul>
                </li>
                <li class="divider"></li>
                <li class="dropdown-submenu">
                  <a>智能客服</a>
                  <ul class="dropdown-menu">
                    <li id="app_jqr"><router-link class="router-link" tabindex="-1" :to="{name: 'appJqr'}">App端机器人客服</router-link></li>
                    <li id="app_rg"><router-link class="router-link" :to="{name: 'appRg'}">App端人工客服</router-link></li>
                    <li id="app_dhrx"><router-link class="router-link" :to="{name: 'appDhrx'}">电话端热线客服</router-link></li>
                    <li id="kfzj"><router-link class="router-link" :to="{name: 'kfzj'}">客服质检</router-link></li>
                  </ul>
                </li>

              </ul>
            </li>

          </ul>
          <div class="nav navbar-form navbar-right time-picker">
            <el-date-picker
            v-model="timeValue"
            type="daterange"
            align="center"
            unlink-panels
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions"
            size="mini"
            popper-class="time-popper"
            :change="changeTime()"
            >
            </el-date-picker>
          </div>
        </div>

      </div>
    </nav>
    <div class="" style="position: fixed; top: 70px; left: 15px; font-size: 15px; z-index: 1000;">
      <p>【{{pageName[$route.name]}}】</p>
    </div>



    <!--模态框-->
    <div id="modal" class="modal fade bs-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">×</button>
            <h4 class="modal-title" id="myModalLabel">提示</h4>
          </div>
          <div class="modal-body">开始时间不能超过今天，请重新选择！</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal">确定</button>
          </div>
        </div><!-- /.modal-content -->
      </div>
    </div>

  </div>
</template>

<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "sheader",
    mounted() {
      $('.nav .router-link').on('click', function(){
        // $('.navbar-toggle').click() //bootstrap 3.x by Richard
        if($('.navbar-toggle').css('display') !== 'none'){
          $('.navbar-toggle').trigger( "click" );
        }
      });
      localStorage.clear();
      localStorage.setItem("startTime","default_start");
      localStorage.setItem("endTime","default_end");
      localStorage.setItem("timeValue", '');

      /*触发组件通信事件*/
      let timeList = ['default_start','default_end'];
      bus.$emit('time', timeList);
    },
    watch: { // 监控路由跳转
      '$route' (to, from) {
        console.log(from);
        this.gotop();
      }
    },
    data() {
      return {
        pageName: {
          'index': '首页',
          'notFound': '404',

          'kfPrediction': '客服预测',
          'commentTag': '短语提取',
          'judgeZcqx': '专车取消判责',
          'judgeZcjz': '专车拒载判责',

          'judgeKcqlqx': '快车全量取消判责',
          'judgeKcjz': '快车拒载判责',
          'judgeKcss': '快车申诉判责',
          'judgeKcimqx': '快车IM取消判责',

          'judgeAqqd': '安全切单判责',
          'judgeAqhc': '安全换车判责',

          'judgeSfc': '顺风车判责',
          'judgeDj': '代驾判责',

          'judgeDjhs': '代驾话术检查',
          'judgeZchs': '专车话术检查',

          'driverBroadcast': '司机端语音语音播报',
          'onlineSfc': '在线合成统计：顺风车',
          'onlineIvr': '在线合成统计：IVR',

          'speechTotal': '在线语音识别总量',
          'speechKc': '识别快车',
          'speechCzc': '识别出租车',
          'speechHhc': '识别豪华车',
          'speechDj': '识别代驾',
          'speechZc': '识别专车',

          'appJqr': 'APP端机器人客服',
          'appRg': 'APP端人工客服',
          'appDhrx': '电话端热线客服',
          'kfzj': '客服质检',
        },
        pickerOptions: { // 时间选择器的选项
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              end.setTime(end.getTime() - 3600 * 1000 * 24 );
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        timeValue: '' // datePicker绑定的值
      }
    },
    methods: {
      gotop() {
        // 每次路由跳转自动回到顶部
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      },
      changeTime() {
        if (this.timeValue) {
          let newTime = this.timeValue[0] + ',' + this.timeValue[1];
          // 不知为啥，路由跳转的时候就算时间不变，也会触发这个change函数，导致每次路由跳转都会重新触发组件传值emit，发送多余的请求，所以手动做一下时间是否改变的判断
          if (newTime !== localStorage.getItem("timeValue")) {
            // 自动收起导航条菜单
            if($('.navbar-toggle').css('display') !== 'none'){
              $('.navbar-toggle').trigger( "click" );
            }

            let today = new Date();
            if (this.timeValue[0] > today) { // 开始时间超过了今天，使用模态框报错

              // 弹出模态框
              $('#modal').modal('show');

              this.timeValue = '';
              localStorage.setItem("startTime", "default_start");
              localStorage.setItem("endTime", "default_end");

              /*触发组件通信事件*/
              let timeList = ['default_start', 'default_end'];
              bus.$emit('time', timeList);

            }
            else {
              if (this.timeValue[1] > today) { // 结束时间超过了今天，自动将结束时间设为今天
                this.timeValue[1] = today;
              }
              let startYear = this.timeValue[0].getFullYear();
              let startMonth = this.timeFormat(this.timeValue[0].getMonth() + 1);
              let startDate = this.timeFormat(this.timeValue[0].getDate());
              let endYear = this.timeValue[1].getFullYear();
              let endMonth = this.timeFormat(this.timeValue[1].getMonth() + 1);
              let endDate = this.timeFormat(this.timeValue[1].getDate());
              let startTime = startYear + startMonth + startDate;
              let endTime = endYear + endMonth + endDate;

              localStorage.setItem("startTime", startTime);
              localStorage.setItem("endTime", endTime);

              localStorage.setItem("timeValue", this.timeValue);

              /*触发组件通信事件*/
              let timeList = [startTime, endTime];
              bus.$emit('time', timeList);
            }
          }
        }
      },
      timeFormat(value) { // 小于0的月份和日期的格式化
        if (parseInt(value) < 10) {
          return '0' + value
        }
        else
          return value;
      }
    }
  }
</script>

<style scoped>
  .navbar-header, ul, li {
    cursor: pointer;
  }

  /*设置hover下拉而非点击下拉*/
  ul.nav li.dropdown:hover > ul.dropdown-menu {
    display: block;
  }
  /*设置二级菜单生效*/
  .dropdown-submenu {
    position: relative;
  }
  .dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: -1px;
    -webkit-border-radius: 0 6px 6px 6px;
    -moz-border-radius: 0 6px 6px;
    border-radius: 0 6px 6px 6px;
  }
  .dropdown-submenu:hover > .dropdown-menu {
    display: block;
  }
  .dropdown-submenu > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: #ccc;
    margin-top: 5px;
    margin-right: -10px;
  }
  .dropdown-submenu:hover > a:after {
    border-left-color: #fff;
  }
  .dropdown-submenu.pull-left {
    float: none;
  }
  .dropdown-submenu.pull-left > .dropdown-menu {
    left: -100%;
    margin-left: 10px;
    -webkit-border-radius: 6px 0 6px 6px;
    -moz-border-radius: 6px 0 6px 6px;
    border-radius: 6px 0 6px 6px;
  }
  /*时间选择器样式设置*/
  /*.time-picker {*/
    /*!*font-size: 14px;*!*/
    /*!*width: 0;*!*/
    /*margin: auto 0;*/
    /*line-height: 50px;*/
    /*height: 50px;*/
  /*}*/
  /*时间选择器的下拉框*/
  /*.time-popper {*/
    /*width: 100px;*/
    /*height: 100px;*/
  /*}*/

  /*手动改变头部导航栏的默认宽度*/
  @media (max-width: 1085px) {
    .navbar-header {
      float: none;
    }
    .navbar-toggle {
      display: block;
    }
    .navbar-collapse {
      border-top: 1px solid transparent;
      box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
    }
    .navbar-collapse.collapse {
      display: none!important;
    }
    .navbar-nav {
      float: none!important;
      margin: 7.5px -15px;
    }
    .navbar-form {
      float: none!important;
      margin: 7.5px -15px;
    }
    .navbar-nav>li {
      float: none;
    }
    .navbar-nav>li>a {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .navbar-text {
      float: none;
      margin: 15px 0;
    }
    /* since 3.1.0 */
    .navbar-collapse.collapse.in {
      display: block!important;
    }
    .collapsing {
      overflow: hidden!important;
    }
  }
</style>



