import Vue from 'vue'
import Router from 'vue-router'

import notFound from '@/components/NotFound' // 404
import index from '@/components/index' // 首页

import kfPrediction from '../components/kfRelated/kfPrediction' // 客服相关-客服预测
import commentTag from '../components/kfRelated/commentTag' // 客服相关-短语提取

import judgeZcqx from '../components/requestForCancel/judgeZcqx' // 取消判责-专车取消
import judgeZcjz from '../components/requestForCancel/judgeZcjz' // 取消判责-专车拒载

// 快车取消判责
import judgeKcqlqx from '../components/requestForCancel/judgeKcqlqx' // 取消判责-快车全量取消
import judgeKcjz from '../components/requestForCancel/judgeKcjz' // 取消判责-快车拒载
import judgeKcss from '../components/requestForCancel/judgeKcss' // 取消判责-快车申诉
import judgeKcimqx from '../components/requestForCancel/judgeKcimqx' // 取消判责-快车im取消

import judgeAqqd from '../components/requestForCancel/judgeAqqd' // 取消判责-安全切单判责
import judgeAqhc from '../components/requestForCancel/judgeAqhc' // 取消判责-安全换车判责

import judgeSfc from '../components/requestForCancel/judgeSfc' // 取消判责-顺风车判责
import judgeDj from '../components/requestForCancel/judgeDj' // 取消判责-代驾判责

import judgeDjhs from '../components/requestForCancel/judgeDjhs' // 取消判责-代驾话术检查
import judgeZchs from '../components/requestForCancel/judgeZchs' // 取消判责-专车话术检查

import driverBroadcast from '../components/speechCompose/driverBroadcast' // 司机端语音合成
import onlineSfc from '../components/speechCompose/onlineSfc' // 在线合成-在线合成统计：顺风车
import onlineIvr from '../components/speechCompose/onlineIvr' // 在线合成-在线合成统计：ivr

import speechTotal from '../components/speechRecog/speechTotal' // 语音识别-在线语音识别总量
import speechKc from '../components/speechRecog/speechKc' // 语音识别-语音叫车-识别快车
import speechCzc from '../components/speechRecog/speechCzc' // 语音识别-语音叫车-识别出租车
import speechHhc from '../components/speechRecog/speechHhc' // 语音识别-语音叫车-识别豪华车
import speechDj from '../components/speechRecog/speechDj' // 语音识别-语音叫车-识别代驾
import speechZc from '../components/speechRecog/speechZc' // 语音识别-语音叫车-识别专车

import appJqr from '../components/speechRecog/appJqr' // 语音识别-智能客服-机器人
import appRg from '../components/speechRecog/appRg' // 语音识别-智能客服-人工
import appDhrx from '../components/speechRecog/appDhrx' // 语音识别-智能客服-电话热线
import kfzj from '../components/speechRecog/kfzj' // 语音识别-智能客服-客服质检



Vue.use(Router);

export default new Router({
  // mode: 'hash',
  routes: [
    { path: '*', name: 'notFound',component: notFound }, //404
    { path: '/', name: 'index', component: index }, // 首页

    { path: '/kfPrediction', name: 'kfPrediction', component: kfPrediction }, // 客服相关-客服预测
    { path: '/commentTag', name: 'commentTag', component: commentTag }, // 客服相关-短语提取

    { path: '/judgeZcqx', name: 'judgeZcqx', component: judgeZcqx }, // 取消判责-专车取消
    { path: '/judgeZcjz', name: 'judgeZcjz', component: judgeZcjz }, // 取消判责-专车拒载

    { path: '/judgeKcqlqx', name: 'judgeKcqlqx', component: judgeKcqlqx }, // 取消判责-快车拒载
    { path: '/judgeKcjz', name: 'judgeKcjz', component: judgeKcjz }, // 取消判责-快车拒载
    { path: '/judgeKcss', name: 'judgeKcss', component: judgeKcss }, // 取消判责-快车申诉
    { path: '/judgeKcimqx', name: 'judgeKcimqx', component: judgeKcimqx }, // 取消判责-快车im取消

    { path: '/judgeAqqd', name: 'judgeAqqd', component: judgeAqqd }, // 取消判责-安全切单判责
    { path: '/judgeAqhc', name: 'judgeAqhc', component: judgeAqhc }, // 取消判责-安全换车判责

    { path: '/judgeSfc', name: 'judgeSfc', component: judgeSfc }, // 取消判责-代驾判责
    { path: '/judgeDj', name: 'judgeDj', component: judgeDj }, // 取消判责-代驾判责

    { path: '/judgeDjhs', name: 'judgeDjhs', component: judgeDjhs }, // 取消判责-代驾话术检查
    { path: '/judgeZchs', name: 'judgeZchs', component: judgeZchs }, // 取消判责-专车话术检查

    { path: '/driverBroadcast', name: 'driverBroadcast', component: driverBroadcast }, // 在线合成-离线合成统计-司机端语音播报
    { path: '/onlineSfc', name: 'onlineSfc', component: onlineSfc }, // 在线合成-在线合成统计：顺风车
    { path: '/onlineIvr', name: 'onlineIvr', component: onlineIvr }, // 取消判责-在线合成统计：ivr

    { path: '/speechTotal', name: 'speechTotal', component: speechTotal }, // 语音识别-在线语音识别总量
    { path: '/speechKc', name: 'speechKc', component: speechKc }, // 语音识别-语音叫车-识别快车
    { path: '/speechCzc', name: 'speechCzc', component: speechCzc }, // 语音识别-语音叫车-识别出租车
    { path: '/speechHhc', name: 'speechHhc', component: speechHhc }, // 语音识别-语音叫车-识别豪华车
    { path: '/speechDj', name: 'speechDj', component: speechDj }, // 语音识别-语音叫车-识别代驾
    { path: '/speechZc', name: 'speechZc', component: speechZc }, // 语音识别-语音叫车-识别专车

    { path: '/appJqr', name: 'appJqr', component: appJqr }, // 语音识别-智能客服-机器人
    { path: '/appRg', name: 'appRg', component: appRg }, // 语音识别-智能客服-人工
    { path: '/appDhrx', name: 'appDhrx', component: appDhrx }, // 语音识别-智能客服-电话热线
    { path: '/kfzj', name: 'kfzj', component: kfzj }, // 语音识别-智能客服-客服质检

  ],

  mode: 'hash',
  base: __dirname
})




