<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3>目录</h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="prediction_1_a" @click="goTag('#prediction_1')" class=""><a>智能预测PV统计（小时-汇总）</a></li>
          <li id="prediction_2_a" @click="goTag('#prediction_2')"><a>智能预测PV统计（小时-产品）</a></li>
          <li id="prediction_3_a" @click="goTag('#prediction_3')"><a>智能预测PV统计（按天-汇总）</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header">客服预测</h1>

        <div class="form-container col-sm-12 col-md-11">
          <div id="prediction_1" class="form" ></div>
          <div id="prediction_2" class="form"></div>
          <div id="prediction_3" class="form"></div>

        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "kfPrediction",
    data() {
      return {
        data: {
          prediction_1: {},
          prediction_2: {},
          prediction_3: {},
        },
      }
    },
    mounted() {
      // 先画框架，并设置showLoading
      this.drawPredict1();
      this.drawPreditc2();
      this.drawPreditc3();

      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.prediction_1.showLoading();
        this.prediction_2.showLoading();
        this.prediction_3.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/kfrelated',
        {
          params: {
            page: 'kfprediction',
            start: startTime,
            end: endTime
          }
        }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });


      },
      drawPredict1() { // 第一个图表
        // 基于准备好的dom，初始化echarts实例
        this.prediction_1 = this.$echarts.init($("#prediction_1")[0]);
        // 绘制图表1
        this.prediction_1.setOption({
          title: {
            text: '智能预测PV统计',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'小时-汇总',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'预测成功数'}, {name:'预测失败数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '预测成功数',
            type: 'bar',
            smooth:'true',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{ color:'#000' }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          },
            {
              name:'预测失败数',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data: [],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{ color:'#000'}
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }]
        });
        this.prediction_1.showLoading();
      },
      drawPreditc2() {
        this.prediction_2 = this.$echarts.init($("#prediction_2")[0]);

        this.prediction_2.setOption({
          title: {
            text: '智能预测PV统计',
            subtext:'小时-产品',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'快车-乘客端'}, {name:'快车-司机端'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['stack', 'tiled' ]},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: [],
            silent: false,
            splitLine: {
              show: false
            }
          },
          yAxis: {type: 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '快车-乘客端',
            type: 'bar',
            smooth:'true',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            },
            animationDelay: function (idx) {
              return idx * 10;
            }
          },
            {
              name:'快车-司机端',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data: [],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              },
              animationDelay: function (idx) {
                return idx * 10 + 100;
              }
            }],
          animationEasing: 'elasticOut',
          animationDelayUpdate: function (idx) {
            return idx * 5;
          }
        });
        this.prediction_2.showLoading();
      },
      drawPreditc3() {
        this.prediction_3 = this.$echarts.init($("#prediction_3")[0]);
        this.prediction_3.setOption({
          title: {
            text: '智能预测PV统计',
            subtext:'按天-汇总',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'成功数'}, {name:'失败数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type: 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '成功数',
            type: 'bar',
            smooth:'true',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          },
            {
              name:'失败数',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }]
        });
        this.prediction_3.showLoading();
      },
      fillData() {
        this.prediction_1.hideLoading();
        this.prediction_2.hideLoading();
        this.prediction_3.hideLoading();

        this.prediction_1.setOption({
          xAxis: {
            data: this.data.date_hour
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '预测成功数',
            data: this.data.hour_succ
          },
            {
              name:'预测失败数',
              data: this.data.hour_fail
            }]
        });
        this.prediction_2.setOption({
          xAxis: {
            data: this.data.date_hour
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '快车-乘客端',
            data: this.data.hour_kc_psg
          },
            {
              name:'快车-司机端',
              data: this.data.hour_kc_drv
            }]
        });
        this.prediction_3.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '成功数',
            data: this.data.day_succ
          },
            {
              name:'失败数',
              data: this.data.day_fail
            }]
        });

      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>



