          <li id="num_hour_a" @click="goTag('#num_hour')" class=""><a>离线语音合成次数统计 按小时</a></li>
          <li id="num_day_a" @click="goTag('#num_day')"><a>离线语音合成次数统计 按天</a></li>
          <li id="usr_num_a" @click="goTag('#usr_num')"><a>在线活跃用户数 按天</a></li>
          <li id="error_num_a" @click="goTag('#error_num')"><a>离线语音合成错误次数统计 按天</a></li>
