          <!--离线语音合成次数统计 按小时-->
          <div id="num_hour" class="form" ></div>
          <!--离线语音合成次数统计 按天-->
          <div id="num_day" class="form"></div>
          <!--在线活跃用户数 按天-->
          <div id="usr_num" class="form"></div>
          <!--离线语音合成错误次数统计-->
          <div id="error_num" class="form"></div>
        </div>
      </div>
    </div>
