<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "driverBroadcast",
    data() {
      return {
        data: {
          num_hour: {},
          num_day: {},
          usr_num: {},
          error_num: {},
        },
      }
    },
    mounted() {
      // 先画框架，并设置showLoading
      this.draw_num_hour();
      this.draw_num_day();
      this.draw_usr_num();
      this.draw_error_num();


      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.num_hour.showLoading();
        this.num_day.showLoading();
        this.usr_num.showLoading();
        this.error_num.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/speech_compose',
          {
            params: {
              page: 'driver_broadcast',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
