              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'按小时',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:['Android', 'iOS', 'sum']
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
