                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          },
            {
              name:'iOS',
              type:'line',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
