                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }
          ]
        });
        this.error_num.showLoading();
      },


      fillData() {
        this.num_hour.hideLoading();
        this.num_day.hideLoading();
        this.usr_num.hideLoading();
        this.error_num.hideLoading();


        this.num_hour.setOption({
          xAxis: {
            data: this.data.date_hour
          },
          series: [{
            // 根据名字对应到相应的系列
            name: 'Android',
            data: this.data.Android_hour
          },
            {
              name:'iOS',
              data: this.data.iOS_hour
            },
            {
              name:'sum',
              data: this.data.sum_hour
            }
          ]
        });
        this.num_day.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: 'Android',
            data: this.data.Android_day
          },
            {
              name:'iOS',
              data: this.data.iOS_day
            },
            {
              name:'sum',
              data: this.data.sum_day
            }
          ]
        });
        this.usr_num.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '用户数',
            data: this.data.active_user_day
          }
          ]
        });
        this.error_num.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: 'Android',
            data: this.data.Android_error
          },
            {
              name:'iOS',
              data: this.data.iOS_error
            },
            {
              name:'sum',
