<template>
  <div id="app" style="height: 100%">
    <sHeader></sHeader>
    <router-view class="content"/>
    <gotoTop></gotoTop>
    <sFooter></sFooter>
  </div>
</template>

<script>
import sHeader from "./components/layouts/SHeader"
import sFooter from "./components/layouts/SFooter"
import gotoTop from "./components/gotoTop"
export default {
  name: 'App',
  components: {
    sHeader,sFooter,gotoTop
  },
  mounted() {
  },

}
</script>

<style lang="less">
#app {
  /*font-size: 14px;*/
  min-height: 100%;
  .content {
    min-height: calc(100% - 85px);
    margin-bottom: 40px;
    padding: 0 20px;
    .form-container {
      padding: 10px;
      .form {
        padding: 10px;
        min-height: 400px;
        border: #f0f0f0 solid 2px;
        border-radius: 10px;
        margin-bottom: 30px;
      }
      .form:hover {
        border: #9d9d9d solid 2px;
      }
    }
    .form-container:after {
      content: "";
      display: block;
      height: 0;
      clear: both;
      visibility: hidden;
    }
  }

}
</style>





