        this.requests_num.hideLoading();
        this.succ_num.hideLoading();
        this.fail_num.hideLoading();
        // this.im_num.hideLoading();
        this.download_fail.hideLoading();
        this.dis_result_100.hideLoading();
        this.dis_result.hideLoading();
        this.speech_num.hideLoading();

        this.requests_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '请求数',
            data: this.data.pv
          }]
        });
        this.succ_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '成功数',
            data: this.data.succ
          }]
        });
        this.fail_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '失败数',
            data: this.data.fail
          },
            {
              // 根据名字对应到相应的系列
              name: '调用号码保护平台失败数',
              data: this.data.datanull
            },
            {
              // 根据名字对应到相应的系列
              name: '未产生通话数',
              data: this.data.recordempty
            }]
        });
        // this.im_num.setOption({
        //   xAxis: {
        //     data: this.data.odate
        //   },
        //   series: [{
        //     // 根据名字对应到相应的系列
        //     name: 'IM消息获取数',
        //     data: this.data.ext1
        //   }]
        // });
        this.download_fail.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '语音下载失败数',
            data: this.data.downfail
          }]
        });
        this.dis_result_100.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '司机责任',
            data: this.data.result0_100
          },
            {
              // 根据名字对应到相应的系列
              name: '乘客责任',
              data: this.data.result1_100
            },
            {
              // 根据名字对应到相应的系列
              name: '双方无责',
              data: this.data.result2_100
            },
            {
              // 根据名字对应到相应的系列
              name: '判断不清',
              data: this.data.result3_100
            }]
        });
        this.dis_result.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '司机责任',
            data: this.data.result0
          },
            {
              // 根据名字对应到相应的系列
              name: '乘客责任',
              data: this.data.result1
            },
            {
              // 根据名字对应到相应的系列
              name: '双方无责',
              data: this.data.result2
            },
            {
              // 根据名字对应到相应的系列
              name: '判断不清',
              data: this.data.result3
            }]
        });
        this.speech_num.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '识别数',
            data: this.data.asrcnt
          },
            {
              // 根据名字对应到相应的系列
              name: '失败数',
              data: this.data.asrerr
            }]
        });
      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>



