<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3>目录</h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="comment_tag_1_a" @click="goTag('#comment_tag_1')" class=""><a>短语生成请求数</a></li>
          <li id="comment_tag_2_a" @click="goTag('#comment_tag_2')"><a>短语生成成功数</a></li>
          <li id="comment_tag_3_a" @click="goTag('#comment_tag_3')"><a>短语生成失败数</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header">短语提取</h1>

        <div class="form-container col-sm-12 col-md-11">
          <div id="comment_tag_1" class="form" ></div>
          <div id="comment_tag_2" class="form"></div>
          <div id="comment_tag_3" class="form"></div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "commentTag",
    data() {
      return {
        data: {
          comment_tag_1: {},
          comment_tag_2: {},
          comment_tag_3: {},
        },
        startTime: '',
        endTime: ''
      }
    },
    mounted() {
      this.drawComment_1();
      this.drawComment_2();
      this.drawComment_3();

      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {

        this.comment_tag_1.showLoading();
        this.comment_tag_2.showLoading();
        this.comment_tag_3.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });

    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/kfrelated',
          {
            params: {
              page: 'comment_tag',
              start: startTime,
              end: endTime
          }
          }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });


      },
      drawComment_1() { // 第一个图表
        // 基于准备好的dom，初始化echarts实例
        this.comment_tag_1 = this.$echarts.init($("#comment_tag_1")[0]);
        // 绘制图表1
        this.comment_tag_1.setOption({
          title: {
            text: '短语生成请求数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'请求数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '请求数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.comment_tag_1.showLoading();
      },
      drawComment_2() {
        this.comment_tag_2 = this.$echarts.init($("#comment_tag_2")[0]);

        this.comment_tag_2.setOption({
          title: {
            text: '短语生成成功数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'成功数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '成功数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.comment_tag_2.showLoading();
      },
      drawComment_3() {
        this.comment_tag_3 = this.$echarts.init($("#comment_tag_3")[0]);
        this.comment_tag_3.setOption({
          title: {
            text: '短语生成失败数',
            subtext:''
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'失败数'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type : 'category',
            data: []
          },
          yAxis: {
            type : 'value',
          },
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '失败数',
            type: 'line',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            }
          }]
        });
        this.comment_tag_3.showLoading();
      },
      fillData() {
        this.comment_tag_1.hideLoading();
        this.comment_tag_2.hideLoading();
        this.comment_tag_3.hideLoading();

        this.comment_tag_1.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '请求数',
            data: this.data.pv
          }]
        });
        this.comment_tag_2.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '成功数',
            data: this.data.succ
          }]
        });
        this.comment_tag_3.setOption({
          xAxis: {
            data: this.data.odate
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '失败数',
            data: this.data.fail
          }]
        });
      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>



