<!--在线合成统计 顺风车-->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3>目录</h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="real_time_a" @click="goTag('#real_time')" class=""><a>在线语音合成实时PV统计</a></li>
          <li id="total_a" @click="goTag('#total')"><a>在线语音合成总PV统计</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header">在线合成统计：顺风车</h1>

        <div class="form-container col-sm-12 col-md-11">
          <!--在线语音合成实时PV统计-->
          <div id="real_time" class="form" ></div>
          <!--在线语音合成总PV统计-->
          <div id="total" class="form"></div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "onlineSfc",
    data() {
      return {
        data: {
          real_time: {},
          total: {},
        },
      }
    },
    mounted() {
      // 先画框架，并设置showLoading
      this.draw_real_time();
      this.draw_total();

      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.real_time.showLoading();
        this.total.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/speech_compose',
          {
            params: {
              page: 'online_sfc',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
      draw_real_time() { // 在线语音合成实时PV统计
        this.real_time = this.$echarts.init($("#real_time")[0]);
        this.real_time.setOption({
          title: {
            text: '在线语音合成实时PV统计',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'电话客服',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'电话客服PV'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [
            {
              name:'电话客服PV',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }]
        });
        this.real_time.showLoading();
      },
      draw_total() { // 在线语音合成总PV统计
        this.total = this.$echarts.init($("#total")[0]);

        this.total.setOption({
          title: {
            text: '在线语音合成总PV统计',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'电话客服',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis'},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:[{name:'电话客服PV'}]
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [
            {
              name:'电话客服PV',
              type:'bar',
              barGap:'20%',
              barCategoryGap:'50%',
              smooth:'true',
              data:[],
              markPoint : {
                label:{
                  normal:{
                    position:'top',
                    textStyle:{
                      color:'#000'
                    }
                  }
                },
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            }]
        });
        this.total.showLoading();
      },

      fillData() {
        this.real_time.hideLoading();
        this.total.hideLoading();

        this.real_time.setOption({
          xAxis: {
            data: this.data.date_hour
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '电话客服PV',
            data: this.data.pv
          }
          ]
        });
        this.total.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '电话客服PV',
            data: this.data.sfc_pv
          }
          ]
        });

      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>



