<!--智能客服 客服质检-->
<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-sm-3 col-md-2 sidebar">
        <h3>目录</h3>
        <br>
        <ul class="nav nav-sidebar">
          <li id="req_num_a" @click="goTag('#req_num')" class=""><a>客服质检请求数</a></li>
        </ul>
      </div>
      <div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
        <h1 class="page-header">智能客服 客服质检</h1>

        <div class="form-container col-sm-12 col-md-11">
          <!--客服质检请求数-->
          <div id="req_num" class="form" ></div>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
  import bus from '@/assets/eventBus'
  export default {
    name: "kfzj",
    data() {
      return {
        data: {
          req_num: {},
        },
      }
    },
    mounted() {
      // 先画框架，并设置showLoading
      this.draw_req_num();

      /*监听组件通信事件*/
      this.getData(localStorage.getItem('startTime'), localStorage.getItem('endTime'));
      bus.$on('time', (val) => {
        this.req_num.showLoading();

        let startTime = val[0];
        let endTime = val[1];
        this.getData(startTime, endTime);
      });
    },
    methods: {
      getData(startTime='default_start', endTime='default_end') { // 从接口获取数据
        this.axios.get('/speech_recog',
          {
            params: {
              page: 'kfzj',
              start: startTime,
              end: endTime
            }
          }).then(res =>{
          this.data = res.data;
          // 填充数据
          this.fillData();

        }).catch(err =>{
          console.log(err);
        });
      },
      draw_req_num() { // 请求数
        this.req_num = this.$echarts.init($("#req_num")[0]);
        // 绘制图表1
        this.req_num.setOption({
          title: {
            text: '客服质检请求数',
            textStyle: {
              color:'#533',
              fontFamily:'sans-serif'
            },
            subtext:'',
            subtextStyle:{
              color:'#534',
              fontSize:16
            }
          },
          tooltip: {trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }},
          legend: {
            right:0,
            top:50,
            orient:'vetical',
            data:['请求数']
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {type : 'value'},
          dataZoom: [
            {   // 这个dataZoom组件，默认控制x轴。
              type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
              start: 0,      // 左边在 10% 的位置。
              end: 100         // 右边在 60% 的位置。
            }
          ],
          series: [{
            name: '请求数',
            type: 'bar',
            smooth:'true',
            barWidth:'20%',
            barGap:'50%',
            data: [],
            markPoint : {
              label:{
                normal:{
                  position:'top',
                  textStyle:{
                    color:'#000'
                  }
                }
              },
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type : 'average', name: '平均值'}
              ]
            }
          }]
        });
        this.req_num.showLoading();
      },

      fillData() {
        this.req_num.hideLoading();

        this.req_num.setOption({
          xAxis: {
            data: this.data.date_day
          },
          series: [{
            // 根据名字对应到相应的系列
            name: '请求数',
            data: this.data.day_req
          }]
        });
      }

    },
    destroyed() {
      bus.$off('time');
    }

  }
</script>

<style lang="less" scoped>
</style>





